/* THEME SWITCH */
body.light-theme {
    background-color: $main-section-background-color;
    color: $title-color;
  }
  
  body.dark-theme {
    background-color: #706767;
    color: $title-color;
  }
  
  .toolbar {
    display: flex;
    justify-content: flex-end;
    padding: 0px 20px;
  }
.theme-switch {
    display: flex;
    align-items: center;
  }
  
  .theme-switch__icon {
    color: currentColor;
    height: 18px;
    width: 18px;
    margin-right: 8px;
    margin-left: 8px;
  }
  
  .theme-switch__control {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;
    width: 32px;
    height: 16px;
    border-radius: 50em;
    padding: 3px 0;
  }
  
  .theme-switch__toggle {
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    height: 0;
    width: 0;
    pointer-events: none;
  }
  
  .theme-switch__track {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: transparent;
    user-select: none;
    background-color: #333;
    border-radius: inherit;
    z-index: 1;
    cursor: pointer;
  }
  
  .theme-switch__marker {
    position: relative;
    background-color: $main-section-background-color;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    z-index: 2;
    pointer-events: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.25);
    transform: translateX(3px);
    transition: transform 250ms var(--timing-function);
  }
  
  .theme-switch__toggle:checked + .theme-switch__track {
    background-color: $hover-rating-color;
  }
  
  .theme-switch__toggle:checked ~ .theme-switch__marker {
    transform: translateX(17px);
  }
  
  .theme-switch__toggle:focus + .theme-switch__track,
  .theme-switch__toggle:active + .theme-switch__track {
    box-shadow: 0 0 0 3px $accent-color;
  }