.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    top: 0;
    left: 0;
    opacity: 1;
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 2;
    overflow-y: scroll;
  }
  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
  .modal {
    position: relative;
    transform: scale(1);
    background: $footer-background-color;
    padding: 40px;
    margin: 0 auto;
    transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .modal-signin {
    height: 450px;
  }
  .modal__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 1.15;
    text-align: center;
    letter-spacing: 0.03em;
    color: $pagination-color;
    margin-top: 0;
    margin-bottom: 12px;
  }
  .modal__list-input {
    margin-bottom: 10px;
  }
  .modal__list-item:not(:last-child) {
    margin-bottom: 10px;
  }
  .modal__label {
    position: relative;
    display: flex;
    flex-direction: column;
  
    font-size: 12px;
    line-height: 1.16;
    letter-spacing: 0.01em;
  }
  .modal__field-input {
    height: 40px;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
    margin-top: 4px;
    padding-left: 42px;
    transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      border: 1px solid $accent-color;
      outline: 0;
    }
  }
  
  .modal__button {
    position: relative;
    overflow: hidden;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.87;
    padding: 10px 55px;
    color: $footer-background-color;
    cursor: pointer;
  
    letter-spacing: 0.06em;
    background: $accent-color;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
    border-radius: 4px;
    border: 0;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
    margin-top: 20px;
    transition: All 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:active {
      top: 0.3em;
      left: 0.3em;
      box-shadow: 0 0 0 60px rgba(0, 0, 0, 0.05) inset;
    }
    &::before {
      content: '';
      height: 100px;
      position: absolute;
      transform: translate3d(-120%, -50px, 0) rotate3d(0, 0, 1, 45deg);
      transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
      width: 200px;
    }
    &:hover::before {
      background-color: $footer-background-color;
      transform: translate3d(100%, -100px, 0) rotate3d(0, 0, 1, 90deg);
    }
  }
  .modal__icon {
    position: absolute;
    top: 50%;
    left: 15px;
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    .modal__label:hover &,
    .modal__label:focus & {
      fill: $accent-color;
    }
  }
  
  .user-box {
    display: flex;
    align-items: baseline;
    float: right;
  }
  .user-box__signup,
  .user-box__signin {
    position: relative;
    overflow: hidden;
    border: 1px solid $accent-color;
    padding: 5px;
    border-radius: 4px;
    &:hover {
      background: $accent-color;
    }
    transition: All 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:active {
      top: 0.1em;
      left: 0.1em;
      box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.05) inset;
    }
    &::before {
      content: '';
      height: 60px;
      position: absolute;
      transform: translate3d(-120%, -30px, 0) rotate3d(0, 0, 1, 45deg);
      transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
      width: 120px;
    }
    &:hover {
      background-color: $hover-rating-color;
    }
    &:hover::before {
      background-color:$footer-background-color;
      transform: translate3d(100%, -60px, 0) rotate3d(0, 0, 1, 90deg);
    }
  }
  
  .user-box__signup {
    margin-right: 30px;
  }
  .site-nav__signup {
    position: relative;
    overflow: hidden;
    border: 2px solida $accent-color;
