    }
  }
  .site-nav__signin {
    position: relative;
    overflow: hidden;
    padding: 15px;
    border-radius: 4px;
    border: 2px solid $accent-color;
    transition: All 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:active {
      top: 0.3em;
      left: 0.3em;
      box-shadow: 0 0 0 60px rgba(0, 0, 0, 0.05) inset;
    }
    &::before {
      content: '';
      height: 100px;
      position: absolute;
      transform: translate3d(-100%, -50px, 0) rotate3d(0, 0, 1, 45deg);
      transition: all 1.1s cubic-bezier(0.19, 1, 0.22, 1);
      width: 200px;
    }
    &:hover {
      background-color: $hover-rating-color;
    }
    &:hover::before {
      background-color: $footer-background-color;
      transform: translate3d(100%, -100px, 0) rotate3d(0, 0, 1, 90deg);
    }
  }
  @media screen and (min-width: 768px) {
    .modal {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 450px;
    }
  
    .backdrop.is-hidden .modal {
      transform: translate(-50%, 150%) scale(0.75);
    }
  }
  @media screen and (min-width: 1200px) {
    .modal {
      width: 528px;
      margin: 0;
    }
  }
  .signin-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .signup-spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  
